.navbar {
  background: $secondary;
  @include mobile568 {
    display: none;
  }
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__list {
    display: flex;
  }
  &__item {
    display: block;
    text-decoration: none;
    color: $white;
    font-weight: bold;
    padding: 12px 24px;
    @include desktop {
      padding: 16px;
    }
  }
  li:first-child a {
    padding: 12px 24px 12px 0px;
    @include desktop {
      padding: 16px 16px 16px 0px;
    }
  }
  &__search {
    @include hide-text;
    width: 30px;
    height: 30px;
    background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/102c91f7b3d8e08005073f54b3283b09a18dc427/week4/icon-search.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}
.blog {
  &__title {
    font-size: 48px;
    line-height: 80px;
    font-weight: bold;
    padding-left: 16px;
    border-left: 8px solid $primary;
    margin: 40px 0px 60px;
    @include desktop {
      border-left: 4px solid $primary;
      margin: 32px 0px 40px;
    }
    @include mobile568 {
      font-size: 28px;
      padding-left: 8px;
      margin: 28px 0px 28px;
    }
  }
  &__item {
    display: flex;
    margin-bottom: 60px;
    @include desktop {
      margin-bottom: 24px;
    }
    @include mobile568 {
      flex-direction: column;
      margin-bottom: 20px;
    }
    &__img {
      width: 58.3%;
      height: 400px;
      object-fit: cover;
      margin-right: 30px;
      @include desktop {
        width: 50%;
        height: 208px;
      }
      @include mobile568 {
        width: 100%;
        height: 217px;
      }
    }
    .card {
      &__title {
        color: $primary;
        font-size: 32px;
        line-height: 63px;
        @include desktop {
          font-size: 20px;
          line-height: 29px;
        }
      }
      &__subLine {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;
        @include desktop {
          margin-bottom: 32px;
        }
        &__subTitle {
          font-size: 20px;
          line-height: 30px;
          font-weight: 500;
          @include desktop {
            font-size: 16px;
          }
        }
        &__time {
          font-size: 16px;
          line-height: 24px;
          @include desktop {
            font-size: 14px;
          }
        }
      }
      &__body {
        font-size: 20px;
        line-height: 30px;
        color: #555555;
        margin-bottom: 14px;
        @include desktop {
          font-size: 14px;
          line-height: 21px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 6;
        }
        @include mobile568 {
          display: none;
        }
      }
      &__footer {
        color: $secondary;
        text-decoration: none;
        font-size: 36px;
        line-height: 54px;
        display: block;
        text-align: right;
        font-weight: 900;
        @include desktop {
          color: #555555;
          font-size: 14px;
          line-height: 21px;
        }
        @include mobile568 {
          display: none;
        }
      }
    }
  }
}
